import { IActionContext } from './IAction';
import { IActionInput } from './IActionInput';

/**
 * Data action pre hook
 */
export type ActionPreHook = (
    input: IActionInput | IActionInput[],
    context: IActionContext
) => Promise<void | (IActionInput | IActionInput[])>;
/**
 * Data action post hook
 */
export type ActionPostHook<IActionOut> = (
    input: IActionInput | IActionInput[],
    results: IActionOut | IActionOut[],
    context: IActionContext
) => Promise<void | (IActionOut | IActionOut[])>;

// Read Hook wont create any output.
/**
 * Data action pre readO hook
 */
export type ActionPreReaderHook = (input: IActionInput | IActionInput[], context: IActionContext) => Promise<void>;

/**
 * Data action post read hook
 */
export type ActionPostReaderHook<IActionOut> = (
    input: IActionInput | IActionInput[],
    results: IActionOut | IActionOut[],
    context: IActionContext
) => Promise<void>;
